.pdf-container {
    text-align: center;
    margin-top: 20px;
  }
  
  .pdf-content {
    background-color: #f0f0f0; /* Light gray background color */
    border: 1px solid #ccc;
    padding: 20px;
    margin: 0 auto;
  }
  
  .pdf-content h1 {
    color: #333;
    background-color: #f0f0aa; /* Green background color for the header */
    padding: 10px;
  }
  
  .pdf-content table {
    width: 100%;
    /* border-collapse: collapse; */
    border: 2px solid rgb(11, 10, 10);
    padding: 10px;
    border-radius: 25px;
  }
  
  .pdf-content th,
  .pdf-content td {
    /* border: 1px solid #ccc; */
    padding: 8px;
  }
  
  .pdf-content p {
    margin-top: 20px;
  }
  
  .pdf-image {
    max-width: 100%;
    max-height: 100%;
  }
  